body, html {
  font-family: 'Montserrat', sans-serif;
  background: rgb(101,115,255);
  background: radial-gradient(circle, rgba(101,115,255,1) 0%, rgba(206,107,248,1) 100%);
}

.card {
  width: 400px;
  display: block;
  margin: 60px 30px;
  box-shadow: 10px 5px 40px 20px darken(#341cac, 5%);
  transition: .25s;
}

.card-header {
  height: 200px;
  width: 100%;
  padding: 15px;
  width:100%;
  background-size:cover;
  color:#fff;
}

.card-header--title {
  text-transform: uppercase;
  margin-top: 200px;
  text-align: left;
  color: black;
}

.card-body {
  padding: 15px;
  background-color:#fff;
  width:100%;
}

.date {
  font-size: 11px;
  font-weight: 600;
  color: grey;
}

.body-content {
  padding-bottom: 40px;
  font-size: 13px;
  line-height: 1.8;
}

.button-primary {
  border: none;
  box-shadow: none;
  font-family: inherit;
  background-color:transparent;
  color: #341cac;
  font-size: 15px;
  transition:.25s;
}

.everything {
  display: flex;
  margin-top: 2em;
  margin-bottom: auto;
}

.app-card-list {
  display: inline-block;
  width: 100%;
  float: right;
  margin: 0;
  min-height: 75vh;
  white-space: normal;
  overflow-x: scroll;
  margin-top: -70px;
}
.app-card-list::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 10px;
}
.app-card-list::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(0,0,0,.5);
  -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
}

@media (min-width: 768px) {
  .app-card-list {
    width: 80%;
    white-space: nowrap;
    margin-top: 0;
  }
}
.app-card-list .card {
  margin: 60px 30px;
  display: inline-block;
}
.app-card-list .card .card-body {
  white-space: normal;
}

.app-title {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 2em;
  margin-right: 1em;
}